
html{min-height:100%
}
body{background:linear-gradient(135deg, #ffb948, #ff45ad, #a723ff);background-attachment:fixed;background-color:#a723ff;transition:background 0.5s
}
*{box-sizing:border-box
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#2c3e50;padding:80px 0;margin:0 auto;max-width:1060px;font-size:12px
}
@media (min-width: 800px){
#app{font-size:16px
}
}
.fade-enter-active,.fade-leave-active{transition:all 0.5s ease
}
.fade-enter,.fade-leave-active{opacity:0
}
.fade-enter{transform:translateX(-20px)
}
.fade-leave-active{transform:translateX(20px)
}
.fade-enter-active{transition-delay:0.5s
}
.content{padding:0 0.3em
}
@media (min-width: 800px){
.content{padding:0 30px;float:left;width:calc(100% - 270px);margin-left:270px
}
}
.heading{font-family:Futura, 'Avenir', Helvetica, Arial, sans-serif;font-size:1.5em;color:#fff;margin-bottom:0.8em
}
.loading{font-size:2em;font-weight:bold;text-align:center;color:#fff
}



